@import url(https://fonts.googleapis.com/css?family=Rubik);
body{font-family:'Rubik', sans-serif;background-color:#121212}#root{display:flex;flex-direction:column;justify-content:center;align-items:center}.filter-and-refresh{display:flex;align-items:center;margin-right:3.75rem;color:#ccc}

.header-container{width:48rem;display:flex;flex-direction:row;background-color:#212121;align-items:center;padding:.75rem 1rem .75rem 2.5rem;margin:1.25rem 0;border-radius:6px}.header-text-container{width:80%;margin-bottom:.75rem;color:#ccc}.header-text{display:flex}.header-text :not(last-child){height:.325rem}.yellow-divider{height:.25rem;background-color:#FDBE43;width:85%}.header-image{height:2.25rem;margin-right:3rem}

.filter-container{width:48rem;display:flex;justify-content:flex-end;margin:.75rem}.filter-dialouge{padding:1rem .25rem 0px .25rem;display:flex;flex-direction:column;background-color:#212121;color:#ccc;width:32.5rem}.filter-dialouge legend{color:#ccc}.filter-dialouge legend.Mui-focused{color:#ccc}.filter-dialouge .location-filter-options{color:#ccc;margin-bottom:1.25rem;margin-left:3rem}.filter-dialouge .location-filter-options div{margin-right:.5rem}.filter-dialouge .location-filter-options input{color:#ccc}label,.dialog-title{color:#ccc !important}legend{line-height:0 !important}.form{height:7.5rem;margin-bottom:.75rem;margin-left:3rem}.dialog-title{background-color:#212121;justify-content:flex-end;display:flex}.orange-divider{height:.25rem;background-color:#FF7500;width:98%;align-self:center}

.line-item{width:44rem;background-color:#222222;color:#ccc;margin:0.5rem;padding:1.25rem 2rem;border-radius:0.25rem;height:28px;display:flex;align-content:center;align-items:center;font-size:14px}.fade-in{opacity:0;transition:opacity 0.45s ease;display:flex;align-items:center}.fade-in:not(.show){width:0px}.fade-in:not(.show).arrival-text{color:#222222;width:0px}.fade-in:not(.show) .line-indicator{background-color:#222222}.fade-in:not(.show) .line-text{color:#222222}.fade-in.show{opacity:1}.arrival-text{margin:0px;line-height:2}.line-text{display:flex;align-items:center;align-content:center}.fade-container{display:flex;align-items:center;width:24%;justify-content:flex-start;margin-left:1rem}.info-container{width:60%;display:flex;flex-direction:row}.info-content{margin-right:0.75rem;width:25rem}.info-content.to{max-width:0.75rem}.direction-container{width:20%;display:flex;justify-content:flex-end}@-webkit-keyframes ease{from{opacity:0}to{opacity:1}}@keyframes ease{from{opacity:0}to{opacity:1}}.line-indicator{height:1.25rem;width:1.25rem;border-radius:50%;display:inline-block;margin-right:0.5rem;margin-left:0.75rem}.line-indicator.blue{background-color:#0092d0}.line-indicator.gold{background-color:#fdbe43}.line-indicator.red{background-color:#981b1e}.line-indicator.green{background-color:#0f893b}

.no-matching{white-space:pre-wrap;margin-top:1rem;color:#ccc}.train-schedule-container{align-content:center;align-items:center;display:flex;flex-direction:column}

